body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.favoritesBtn {
  background-color: transparent;
  border: 4px solid #fdde09;
  border-radius: 30px;
  color: #fdde09;
  cursor: pointer;
  display: block;
  font-family: starWars;
  font-size: 28px;
  margin: 0 25%;
  outline: 0;
  padding-bottom: 2px;
  text-align: center;
  text-decoration: none;
  transition: all 0.4s ease-in-out;
  width: 50%;
}

.favoritesBtn:hover {
  background-color: #fdde09;
  border: 4px solid black;
  color: black;
}

.favoritesToggle {
  background-color: #fdde09;
  border: 4px solid black;
  color: black;
}

.favoritesIcon {
  position: relative;
  top: 4px;
}

@media screen and (max-width: 1140px) {
  .favoritesBtn {
    margin: 0 20%;
    width: 60%;
  }
}

@media screen and (max-width: 1000px) {
  .favoritesBtn {
    margin: 20px 20% 10px 20%;
  }
}

@media screen and (max-width: 850px) {
  .favoritesBtn {
    margin: 10px 10%;
    width: 80%;
  }
}

@media screen and (max-width: 420px) {
  .favoritesBtn {
    font-size: 24px;
    padding: 6px 0;
  }
}

.Card {
  border: 6px solid black;
  border-radius: 20px;
  height: 260px;
  margin: 10px;
  overflow: scroll;
  padding: 10px;
  text-align: left;
  width: 300px;
}

ul {
  font-size: 18px;
  line-height: 2.2;
  list-style: none;
  padding-left: 5px;
}

.name {
  border-bottom: 4px solid black;
  font-size: 20px;
  margin: 10px 5px;
  padding-bottom: 10px;
}

.category {
  font-weight: bolder;
}

.favoriteIcon {
  background-color: gainsboro;
  border: 4px solid black;
  border-radius: 20px;
  bottom: 14px;
  color: black;
  cursor: pointer;
  font-family: starWars;
  float: right;
  padding: 0 18px;
  position: relative;
}

.favorited,
.favoriteIcon:hover {
  background-color: black;
  border: 4px solid #fdde09;
  color: #fdde09;
  transition: all 0.4s ease-in-out;
}

@media screen and (max-width: 385px) {
  .Card {
    height: 230px;
    width: 250px;
  }

  ul,
  .name {
    font-size: 15px;
  }

  .favoriteIcon {
    margin-top: 2px;
  }
}

.CardContainer {
  background-color: #fdde09;
  border-radius: 40px;
  display: flex;
  flex-wrap: wrap;
  height: 60vh;
  justify-content: space-evenly;
  margin: 0 auto;
  opacity: 0.8;
  overflow: scroll;
  padding: 10px;
  width: 90%;
}

.center {
  align-items: center;
  flex-direction: column;
}

.emptyContainerMessage {
  font-family: starWars;
  margin: 0 10px;
  text-align: center;
}

.characterPic {
  height: 30vh;
  mix-blend-mode: multiply;
}

@media screen and (max-width: 1000px) {
  .CardContainer {
    height: 72vh;
  }
}

@media screen and (max-width: 500px) {
  .characterPic {
    height: 200px;
  }
}

.Buttons {
  display: flex;
  height: 0px;
  justify-content: space-evenly;
}

.people,
.vehicles,
.planets {
  background-color: transparent;
  border: 4px solid #fdde09;
  border-radius: 30px;
  color: #fdde09;
  cursor: pointer;
  font-family: starWars;
  font-size: 28px;
  height: 50px;
  outline: 0;
  padding: 0px;
  text-align: center;
  text-decoration: none;
  transition: all 0.4s ease-in-out;
  width: 220px;
}

.people:hover,
.vehicles:hover,
.planets:hover,
.active {
  background-color: #fdde09;
  border: 4px solid black;
  color: black;
}

@media screen and (max-width: 1000px) {
  .Buttons {
    margin: 20px 10%;
  }
}

@media screen and (max-width: 850px) {
  .Buttons {
    flex-direction: column;
    justify-content: initial;
    margin: 8px 0;
  }

  .people,
  .vehicles,
  .planets {
    margin: 8px 10%;
    width: 80%;
  }
}

@font-face {
  font-family: starWars;
  src: url(/SWAPI-Box/static/media/Starjedi.cc3019aa.ttf) format("woff2");
}

.MainPage {
  display: grid;
  grid-template-rows: 15vh 10vh 10vh 65vh;
}

.mainTitle {
  color: #fdde09;
  font-family: starWars;
  font-size: 55px;
  height: 20px;
  margin: 15px 10px 0 10px;
  text-align: center;
}

.mainHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

@media screen and (max-width: 1000px) {
  .MainPage {
    grid-template-rows: 100px 80px 120px 50vh;
  }
}

@media screen and (max-width: 850px) {
  .MainPage {
    grid-template-rows: 100px 64px 250px 50vh;
  }
}

@media screen and (max-width: 590px) {
  .MainPage {
    grid-template-rows: 100px 66px 250px 50vh;
  }

  .mainTitle {
    font-size: 50px;
  }
}

@media screen and (max-width: 520px) {
  .MainPage {
    grid-template-rows: 100px 66px 250px 50vh;
  }
}

@media screen and (max-width: 475px) {
  .mainTitle {
    font-size: 40px;
  }
}

@media screen and (max-width: 380px) {
  .MainPage {
    grid-template-rows: 80px 60px 250px 50vh;
  }

  .mainTitle {
    font-size: 32px;
  }
}
.SideScroll {
  color: #FFF;
  overflow: hidden;
  text-align: justify;
}

.scrollText {
  color: #fdde09;
  font-size: 32px;
  line-height: 1.5;
  margin: 5px;
  position: aboslute;
  text-align: center;
}

.scroll {
  -webkit-animation: scroll 60s infinite;
          animation: scroll 60s infinite;
  position: relative;
}

@-webkit-keyframes scroll {
  0% {
    top: 100vh
  }
  100% {
    display: none;
    top: -1500px;
  }
}

@keyframes scroll {
  0% {
    top: 100vh
  }
  100% {
    display: none;
    top: -1500px;
  }
}

@media screen and (max-width: 1000px) {
  .SideScroll {
    height: 86vh;
  }
}

.App {
  background-attachment: fixed;
  background-image: url(/SWAPI-Box/static/media/stars-bg.a312f81e.jpg);
  display: grid;
  grid-template-columns: 300px 3fr;
  height: 100vh;
}

.hide {
  display: none;
}

@media screen and (max-width: 1000px) {
  .App {
    grid-template-columns: 1fr;
    grid-template-rows: 88vh 100vh;
    height: 220vh;
  }
}

@media screen and (max-width: 850px) {
  .App {
    grid-template-columns: 1fr;
    grid-template-rows: 88vh 100vh;
    height: 235vh;
  }
}

@media screen and (max-width: 400px) {
  .App {
    height: 245vh;
  }
}
