.Card {
  border: 6px solid black;
  border-radius: 20px;
  height: 260px;
  margin: 10px;
  overflow: scroll;
  padding: 10px;
  text-align: left;
  width: 300px;
}

ul {
  font-size: 18px;
  line-height: 2.2;
  list-style: none;
  padding-left: 5px;
}

.name {
  border-bottom: 4px solid black;
  font-size: 20px;
  margin: 10px 5px;
  padding-bottom: 10px;
}

.category {
  font-weight: bolder;
}

.favoriteIcon {
  background-color: gainsboro;
  border: 4px solid black;
  border-radius: 20px;
  bottom: 14px;
  color: black;
  cursor: pointer;
  font-family: starWars;
  float: right;
  padding: 0 18px;
  position: relative;
}

.favorited,
.favoriteIcon:hover {
  background-color: black;
  border: 4px solid #fdde09;
  color: #fdde09;
  transition: all 0.4s ease-in-out;
}

@media screen and (max-width: 385px) {
  .Card {
    height: 230px;
    width: 250px;
  }

  ul,
  .name {
    font-size: 15px;
  }

  .favoriteIcon {
    margin-top: 2px;
  }
}
